import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

import FormKodeBarang from "./Component/FormKodeBarang";
import { connect } from "react-redux";
import { editRosok } from "../actions/rosokAction";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import FormKodeBarangEdit from "./Component/FormKodeBarangEdit";
import { getListBarang } from "../actions/kodeBarangAction";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("jenis/delete/by-kode-jenis/" + kode)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getListBarang())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}
const maptoState = (state) => {
  return {
    KodeBarangList: state.provinsi.kodeBarang,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
class KodeBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      KodeBarang: [],
      response: "",
      display: "none",
      columns: [
        {
          dataField: "kode_kategori",
          text: "Kode Kategori",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kode_jenis",
          text: "Kode Jenis",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar Cetak",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_beli",
          text: "Kadar Beli",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_jual",
          text: "Kadar Jual",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nama_jenis",
          text: "Nama Jenis",
        },

        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_jenis)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-tut="reactour_4"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editRosok(
                        row.kode_jenis,
                        row.kode_kategori,
                        row.nama_jenis,
                        row.kadar_beli,
                        row.kadar_jual,
                        row.kadar_cetak,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  handleSubit(hasil) {
    let data = {
      kode_kategori: hasil.kodeKategori,
      kode_kelompok: "PERHIASAN",
      kode_jenis: hasil.kodeJenis,
      kadar_cetak: hasil.kadarCetak,
      kadar_beli: hasil.kadarBeli,
      nama_jenis: hasil.namaJenis,
      kadar_jual: hasil.kadarJual,
    };
    let dataEdit = {
      kode_kategori: hasil.kodeKategori,
      kode_kelompok: "PERHIASAN",
      kadar_cetak: hasil.kadarCetak,
      kadar_beli: hasil.kadarBeli,
      nama_jenis: hasil.namaJenis,
      kadar_jual: hasil.kadarJual,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("jenis/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => LogData("Data Berhasil Ditambah Di Kode Barang", data))
          .then(() => this.props.dispatch(getListBarang()))
          .then(() => this.props.dispatch(reset("KodeBarang")))
      )
      .catch((err) =>
        this.handleReactive(
          err,
          hasil.kodeJenis,
          dataEdit,
          this.props.dispatch(finishSend()),
          this.props.dispatch(hideModal())
        )
      );
  }
  handleEdit(hasil) {
    let data = {
      kode_kategori: hasil.kodeKategori,
      kode_kelompok: "PERHIASAN",
      kode_jenis: hasil.kodeJenis,
      kadar_cetak: hasil.kadarCetak,
      kadar_beli: hasil.kadarBeli,
      nama_jenis: hasil.namaJenis,
      kadar_jual: hasil.kadarJual,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("subcategory", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getListBarang())
        )
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!").then(() => this.props.dispatch(hideModal()))
      );
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: true });
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("Deleted");
    check
      ? reactive(
          err,
          kode,
          "jenis/reactive/by-kode-jenis/",
          data,
          "jenis/edit/by-kode-jenis/"
        ).then(() => this.props.dispatch(getListBarang()))
      : gagal("Data Gagal Ditambahkan");
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button
              type="button"
              className="btn btn-secondary "
              data-tut="reactour_5"
            >
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kelompok, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Kelompok yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
            `Ini adalah tombol navigasi untuk kembali`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title=" Data Kode Jenis" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormKodeBarangEdit
                        onSubmit={(data) => this.handleEdit(data)}
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <FormKodeBarang
                        onSubmit={(data) => this.handleSubit(data)}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Kode Jenis"
                      : "Tambah data Kode Jenis"
                  }
                />
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          onClick={() => this.tambahModal()}
                          data-tut="reactour_1"
                        >
                          Tambah Data
                          <i className="fa fa-plus ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                <div data-tut="reactour_2">
                  {this.props.KodeBarangList ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.KodeBarangList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right mt-5 mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(KodeBarang);
